<template lang="jade">
  .componentsubscribe
    .ui.container
      .row.BGgray
        .ui.left.aligned.text.container
          h2 
          p
            span vtaiwan
            a(href="https://talk.vtaiwan.tw/c/newsletter" title="" target="_blank") 
            span 
          p email
            .ui.form
              .field
                input(type='text',name="email",v-model="email",placeholder='email')
              .ui.green.submit.button
                |        
              .ui.error.message
      .row.left
        .ui.segment.attached
          h2 
          div(v-for = "(item, index) in Discussion")         
            div.ui.styled.accordion
                div.title
                  i.dropdown.icon
                  | {{Discussion[index].title}}
                div.content
                  Discussion_Comment(:comment_id="Discussion[index].id", :slice="false")
          .ui.basic.button(v-if="more.more_topics_url!=undefined", v-on:click="greet")
            | ...    
</template>

<script>
import Discussion_Comment from './Detail_Topic_Discussion_Comment.vue'
import caxios from '../js/request'
import axios from 'axios';
export default {
  name: 'Home',
  components: {
    Discussion_Comment,
    
  },
  props:['allTopics', 'catagories'],
  data () {
    return {
      email: "",
      content: "",
      Discussion: [],
      more: [],
    }
  },

  methods: {
    getContactus(id) {
      caxios.get('https://talk.vtaiwan.tw/c/newsletter/l/latest.json?page='+ id)
      .then((response) => {
        this.more= response.data['topic_list'];
        let discus = response.data['topic_list'];     
        discus = discus['topics'].slice(1); // ()
        for(let i of discus){
          this.Discussion.push(i)
        }
      })
    },
   
    greet: function (event) {
        this.counter++;
        this.getContactus(this.counter);
    }
  },
  created: function(){
    this.getContactus(0);
  },
  updated: function(){
    var vm = this;
    $('.ui.accordion')
      .accordion()
    ;
    $('.follow.button')
  .api('query')
;
    $('.ui.form')
      .form({
        fields: {
          email: {
            rules: [
              {
                type   : 'email',
                prompt : 'email'
              }
            ]
          },
        },
        onSuccess: function(event, fields) {
            $.ajax({
                type: "POST",
                url: "https://talk.vtaiwan.tw/invites?api_key=ee7e1395d767055387db097d51fab90bfee69b806dc276adcf6a22f691fad5df&api_username=vtaiwaninvite",
                data: { "email": fields.email},
                success: function(){
                    alert('')
                },
                error: function(){
                  alert('')
                }
            });
        }
      })
    ;
  },
}
</script>

<style scoped lang="scss">
@import "../sass/global.scss";
.row.BGgray{
    background-color: #efefef;
    padding-top: 2em;
    margin-bottom: 1em;
}
.left{
    text-align: left;
}

.ui.styled.accordion>.title:first-child {
   font-size: 1.5rem; 
}
.ui.green.button,  {                         //
  background-color: #40B3BF;
  margin-bottom:1em;
}
.ui.container{
  height: 100%;
}
.componentsubscribe{
  min-height: 91vh;
}
.ui.text.container{
  font-family: $main_font;
    font-size: 1 rem;
}
</style>



