<template lang="jade">  

  .ParticipationLink_components
    .urllink(v-if = "ulinkall.length > 0")
      span.urllink(v-for="(item, index) in ulinkall")
        span
          a.ui.teal.icon.button(:href='item.link' target='_blank')
            i.icon(:class="item.icon") 
            span.fat-only 
            | {{ item.text }}
              
</template>

<script>
// import axios from 'axios'
// import $ from 'jQuery'
var main = require('./../filters/index.js')
export default {
  props:['urllink'],
  data () {
    return {
      ulinkall:[],
      data_base_non:[
        {
          icon:"linkify",
          text:""
        }
      ],
      data_base:[
        {
          key: 'hackpad',
          icon:"pencil",
          text:""
        },
        {
          key: 'sayit',
          icon:"book",
          text:""
        },
        {
          key: 'youtube',
          icon:"youtube play",
          text:""
        },
        {
          key: 'livehouse',
          icon:"youtube play",
          text:""
        },
        {
          key: 'pol.is',
          icon:"users",
          text:""
        },
        {
          key: 'talk.vtaiwan.tw',
          icon:"edit",
          text:""
        },
        {
          key: 'app.sli.do',
          icon:"bullhorn",
          text:""
        },
        {
          key: 'PDF',
          icon:"download disk",
          text:"PDF"
        },
        {
          key: 'g0v.github',
          icon:"github alternate",
          text:"GitBook"
        },
      ]
    }
  },

  created:function(a,b){
        
    for(var i=0;i<this.urllink.length;i++){
      var tag=0;
      for(var j=0; j<this.data_base.length ;j++){
        if(this.urllink[i].indexOf(this.data_base[j].key)!=-1){

          let item = {}
          item.link = this.urllink[i]
          item.icon = this.data_base[j].icon
          item.text = this.data_base[j].text
          this.ulinkall.push(item)
          /* data_base */
          // this.ulinkall 
          //   .push("<a href="+this.urllink[i]+" target='_blank' class='ui teal icon button'>"+
          //     this.data_base[j].icon+
          //     "<span class='fat-only'> "+
          //     this.data_base[j].text+
          //     "</span>"+
          //     "</a>"
          //   );
          tag = 1;
        }
      }
      if(tag != 1)  /* data_base  */
      {
        let item = {}
        item.link = this.urllink[i]
        item.icon = this.data_base_non[0].icon
        item.text = this.data_base_non[0].text
        if (/^\[(.*?)\]\((.*)\)/.test(item.link)) {
            item.text = RegExp.$1;
            item.link = RegExp.$2;
        }
        this.ulinkall.push(item)
        // this.ulinkall
        //   .push("<a href="+this.urllink[i]+" target='_blank' class='ui teal icon button'>"+
        //     this.data_base_non[0].icon+
        //     "<span class='fat-only'> "+
        //     this.data_base_non[0].text+
        //     "</span>"+
        //     "</a>"
        //   );
      }            
    }
  }
}

</script>
<style lang="scss">
@import "../sass/global.scss";
a.ui.icon.button{
  background-color: $main_color;
  span {
    font-family: $main-font;
  }
}
</style>



